@import "../../../styles/variables.scss";

.button {
    min-height: 48px;
    border: none;
    border-radius: 24px;
    padding-left: 24px;
    padding-right: 24px;

    transition: 0.3s all;

    background-color: #373b53;
    color: $color-grey-dark;

    &:hover {
        background-color: $color-dark-1;
    }

    &_dark_mode {
        background-color: #373b53;
        color: $color-grey-medium;

        &:hover {
            background-color: $color-dark-3;
        }
    }
}

@media screen and (max-width: $mobile-screen-trigger) {
    .button {
        padding-left: 16px;
        padding-right: 16px;
    }
}
