@import "./variables.scss";
@import "./reset.scss";

@font-face {
  font-family: "Spartan";

  font-weight: 500;
  src: local(""),
    //url('../assets/fonts/Spartan-Medium-500.woff2') format('woff2'), /* Super Modern Browsers */
    url("../assets/fonts/Spartan-Medium-500.woff") format("woff"),
    /* Modern Browsers */ url("../assets/fonts/Spartan-Medium-500.ttf") format("truetype"); /* Safari, Android, iOS */
}

@font-face {
  font-family: "Spartan";

  font-weight: 700;
  src: local(""),
    //url('../assets/fonts/Spartan-Bold-700.woff2') format('woff2'), /* Super Modern Browsers */
    url("../assets/fonts/Spartan-Bold-700.woff") format("woff"),
    /* Modern Browsers */ url("../assets/fonts/Spartan-Bold-700.ttf") format("truetype"); /* Safari, Android, iOS */
}

body {
  font-family: "Spartan", sans-serif;

  background-color: var(#{--body-background-color});
}

html {
  box-sizing: border-box;
}

html,
body {
  ::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    background-color: var(#{--body-background-color});
  }

  ::-webkit-scrollbar-thumb {
    background: var(#{--slider-color});
    border-radius: 4px;
  }

  ::-webkit-scrollbar-thumb:hover {
    background-color: #373b53;
  }
}

button {
  font-family: "Spartan", sans-serif;

  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.25px;
}

input {
  font-family: "Spartan", sans-serif;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}
