:root{--body-background-color-day: #f8f8fb;--body-background-color-dark: #141625;--slider-color-day: #dfe3fa;--slider-color-dark: #252945;--body-background-color: var(--body-background-color-day);--slider-color: var(--slider-color-day)}.select{position:relative;display:flex;flex-wrap:nowrap;align-items:stretch;justify-content:space-between;flex-direction:column}.select .input{background-color:#fff;border:1px solid #dfe3fa;border-radius:4px;min-height:48px;padding-left:20px;padding-right:20px;font-weight:700;font-size:12px;line-height:15px;letter-spacing:-0.25px;color:#0c0e16;cursor:pointer}.select .input:hover{border:1px solid #9277ff}.select .input::placeholder{opacity:0}.select .input[error=true]{border:1px solid #ec5757}.select .arrow_img{position:absolute;top:46px;right:16px;width:12px;height:8px}.select .options{position:absolute;width:100%;background-color:#fff;box-shadow:0px 10px 20px rgba(72,84,159,.25);border-radius:8px;top:84px;left:0;display:none;z-index:3;cursor:pointer}.select .options .option{padding-top:15px;padding-left:24px;height:47px;border-bottom:1px solid #dfe3fa;color:#0c0e16;font-weight:700;font-size:12px;line-height:15px;letter-spacing:-0.25px}.select .options .option:hover{color:#7c5dfa}.select .options .option:last-child{border-bottom:none}.select .visible{display:block}.select .label{position:relative;font-weight:500;font-size:12px;line-height:15px;letter-spacing:-0.25px;color:#7e88c3;padding-bottom:10px}.select .label[error=true]{color:#ec5757}.select .label[error=true]::after{content:"canât be empty";position:absolute;top:0;right:0}.select_dark_mode .input{background:#1e2139;border:1px solid #252945;color:#fff}.select_dark_mode .options{background:#252945;box-shadow:0px 10px 20px rgba(0,0,0,.25)}.select_dark_mode .options .option{color:#fff;border-bottom:1px solid #1e2139}.select_dark_mode .label{color:#dfe3fa}